window.jQuery = function(selectorOrArray) {
  let elements;
  if (typeof selectorOrArray === "string") {
    elements = document.querySelectorAll(selectorOrArray);
  } else if (selectorOrArray instanceof Array) {
    elements = selectorOrArray;
  }

  return {
    oldApi: selectorOrArray.oldApi,

    find(selector) {
      let array = [];
      for (let i = 0; i < elements.length; i++) {
        array = array.concat(
          Array.from(elements[i].querySelectorAll(selector))
        );
      }
      array.oldApi = this; //å…ˆæŠŠç›®å‰çš„jQueryå¯¹è±¡æ”¾åˆ°å³å°†è¦æˆä¸ºæ–°çš„elementsçš„æ•°ç»„ä¸­åŽ»ï¼Œæˆä¸ºä¸€ä¸ªoldApiå±žæ€§
      return jQuery(array); //åœ¨è¿”å›ž æ–°å»ºçš„ æ•°ç»„çš„jQueryå¯¹è±¡
    },

    end() {
      return this.oldApi;
    }, //thisä¸ºæ•°ç»„çš„JQueryå¯¹è±¡äº†ï¼Œä»–æœ‰ä¸ªoldApiå±žæ€§ï¼Œå±žæ€§å€¼ä¸ºselectorOrArrayï¼ˆä¹Ÿå°±æ˜¯æ•°ç»„ï¼‰çš„å±žæ€§oldApi

    addClass(className) {
      for (let i = 0; i < elements.length; i++) {
        elements[i].classList.add(className);
      }
      return this;
    },

    //eachå‡½æ•°éåŽ†elementsä¸­æ¯ä¸ªå…ƒç´ ï¼Œå¹¶è°ƒç”¨è‡ªå†™å‡½æ•°
    each(fn) {
      for (i = 0; i < elements.length; i++) {
        fn.call(null, elements[i], i);
      }
      return this;
    },

    //parentå‡½æ•°ç”¨äºŽæ‰¾elementsä¸­å…ƒç´ çš„çˆ¸çˆ¸
    parent() {
      const array = []; //æ–°å»ºç©ºæ•°ç»„ï¼ŒæŠŠæ¯ä¸ªå…ƒç´ çš„çˆ¸çˆ¸æŽ¨è¿›æ¥
      this.each(node => {
        if (array.indexOf(node.parentNode) === -1)
          //è¿™ä¸ªnodeçš„çˆ¸çˆ¸çš„æ•°ç»„é‡Œçš„ä¸‹æ ‡ä¸º-1ï¼Œè¡¨ç¤ºæ ¹æœ¬ä¸åœ¨é‡Œé¢ï¼Œæ‰è¿›è¡Œpush//æˆ‘ä¸çŸ¥é“ä¸ºå•¥è¦è¿™æ ·åšï¼Œä½ æœ¬æ¥å°±æ˜¯æ‰¾æ¯ä¸ªå…ƒç´ çš„çˆ¸çˆ¸ï¼Œä¸ºå•¥ç›¸åŒçš„çˆ¸çˆ¸è¿˜è¦å½’ä¸ºä¸€ä¸ªï¼Ÿ
          array.push(node.parentNode);
      }); //å¯¹å½“å‰çš„jQueryå¯¹è±¡è°ƒç”¨eachå‡½æ•°ï¼Œæ¯ä¸ªelementså…ƒç´ éƒ½æ‰§è¡Œå‡½æ•°ï¼šæ‰¾åˆ°çˆ¸çˆ¸å¹¶ä¸”æŽ¨åˆ°æ•°ç»„arrayä¸­
      array.oldApi = this;
      return jQuery(array);
    },

    //printå‡½æ•°ç”¨äºŽæ‰“å°å‡ºelements
    print() {
      console.log(elements);
    },

    //childrenå‡½æ•°
    /*children() {     æˆ‘çš„æ–¹æ³•æ¯”è€å¸ˆçš„å¤æ‚ç‚¹
      let array = [];   è¦ç”¨letå£°æ˜Žæ•°ç»„ï¼Œå› ä¸ºåŽé¢ä¼šå˜
      this.each(node => {
        array = array.concat(Array.from(node.children));
        console.log(array);
      });
      return jQuery(array);
    }*/
    children() {
      //è€å¸ˆçš„ä»£ç æ›´å°‘
      const array = [];
      this.each(node => {
        array.push(...node.children);
      });
      return jQuery(array);
    },

    //siblingså‡½æ•°æ‰¾å…„å¼Ÿ
    siblings() {
      let array = [];
      this.each(node => {
        array = array.concat(
          Array.from(node.parentNode.children).filter(n => n !== node)
        );
      });
      return jQuery(array);
    },

    //indexå‡½æ•°èŽ·å–æŽ’è¡Œè€å‡ 
    index() {
      array = []; //å®¹çº³æ¯ä¸ªå…ƒç´ çš„æŽ’å
      this.each(node => {
        const list = node.parentNode.children;

        let m;
        for (m = 0; m < list.length; m++) {
          if (list[m] === node) {
            array.push(m);
            break;
          }
        }
      });

      return jQuery(array);
    },

    //nextå‡½æ•°ç”¨äºŽèŽ·å–ç¬¬ä¸€ä¸ªå¼Ÿå¼Ÿ
    next() {
      const array = [];
      this.each(node => {
        array.push(node.nextElementSibling);
      });
      return jQuery(array);
    },
    //prevå‡½æ•°ç”¨äºŽèŽ·å–å‰ä¸€ä¸ªå“¥å“¥
    prev() {
      const array = [];
      this.each(node => {
        array.push(node.previousElementSibling);
      });
      return jQuery(array);
    }
  };
};
